import React, { useState, useEffect } from 'react';
import { axiosInstance } from "../store/request";  // Assuming this is your API setup
import './create-liga-user.css';

const UserDialog = ({ isOpen, onClose, onSave }) => {
    const [nickname, setNickname] = useState('');
    const [league, setLeague] = useState('');
    const [leagues, setLeagues] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isLoadingLeagues, setIsLoadingLeagues] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (isOpen) {
            document.body.style.overflow = 'hidden';
            fetchLeagues();
        } else {
            document.body.style.overflow = 'unset';
        }

        return () => {
            document.body.style.overflow = 'unset';
        };
    }, [isOpen]);

    const fetchLeagues = async () => {
        setIsLoadingLeagues(true);
        setError(null);
        try {
            const response = await axiosInstance.get('/leagues/v1/getAllLeagues');
            setLeagues(response.data);
        } catch (err) {
            setError('砖 注转 转');
            console.error('Error fetching leagues:', err);
        } finally {
            setIsLoadingLeagues(false);
        }
    };

    const handleSave = async () => {
        if (!nickname || !league) {
            alert("  转  砖转");
            return;
        }

        setIsLoading(true);
        try {
            await onSave({ nickname, league });
            onClose();
        } catch (error) {
            alert("专注 砖 转 专砖");
            console.error('Error saving user data:', error);
        } finally {
            setIsLoading(false);
        }
    };

    if (!isOpen) return null;

    return (
        <div className={`dialog-overlay ${isOpen ? 'open' : ''}`}>
            <div className="dialog-container">
                {/* Header */}
                <div className="dialog-header">
                    <h2 className="dialog-title">专砖 砖拽</h2>
                </div>

                {/* Form Content */}
                <div>
                    {/* Nickname Input */}
                    <div className="form-group">
                        <label className="form-label"></label>
                        <input
                            type="text"
                            className="form-input"
                            placeholder="住 "
                            value={nickname}
                            onChange={(e) => setNickname(e.target.value)}
                        />
                    </div>

                    {/* League Select */}
                    <div className="form-group">
                        <label className="form-label"></label>
                        {isLoadingLeagues ? (
                            <div className="loading-spinner-small"></div>
                        ) : error ? (
                            <div className="error-message">
                                {error}
                                <button
                                    className="retry-button"
                                    onClick={fetchLeagues}
                                >
                                    住 砖
                                </button>
                            </div>
                        ) : (
                            <select
                                className="form-select"
                                value={league}
                                onChange={(e) => setLeague(e.target.value)}
                                disabled={isLoadingLeagues}
                            >
                                <option value="">专 </option>
                                {leagues.map((league) => (
                                    <option
                                        key={league.id}
                                        value={league.id}
                                    >
                                        {league.name}
                                    </option>
                                ))}
                            </select>
                        )}
                    </div>
                </div>

                {/* Footer */}
                <div className="dialog-footer">
                    <button
                        className="btn btn-primary"
                        onClick={handleSave}
                        disabled={isLoading || !nickname || !league}
                    >
                        {isLoading ? (
                            <>
                                <span className="loading-spinner"></span>
                                砖专...
                            </>
                        ) : (
                            '砖专'
                        )}
                    </button>
                </div>
            </div>
        </div>
    );
};

export default UserDialog;