import React, {useEffect, useState} from 'react';
import './profile.css';
import BottomMenu from "./BottomMenu";
import {axiosInstance} from "../store/request";
import {userLigaApi} from "../store/apis";

const UserProfile = () => {
    const [userData, setUserData] = useState({});
    const [betHistory, setBetHistory] = useState([]);
    useEffect(() => {
        fetchUser();
    }, []);

    async function fetchUser(){
        const [userResponse] = await Promise.all([
            userLigaApi()
        ]);
        setUserData(userResponse)
    }


    return (
        <div className="profile-container">
            <div className="profile-card">
                <div className="profile-header">
                    <div className="profile-main-info">
                        <div>
                            <div className="profile-name-section">
                                    <>
                                        <h2 className="profile-nickname">{userData.nickname}</h2>
                                    </>
                            </div>
                            <div className="league-selection">
                                <span>拽转: {userData.points}</span>
                            </div>
                        </div>
                        <div className="stats-section">
                            <div className="stat-item"> 爪: {userData.winRate}%</div>
                            <div className="stat-item">专: {userData.totalBets}</div>
                        </div>
                    </div>
                </div>

                {/*<div className="personal-info">*/}
                {/*    <h3 className="section-title">驻专 砖</h3>*/}
                {/*    <div className="info-grid">*/}
                {/*        <div className="info-item">*/}
                {/*            <span className="info-label"></span>*/}
                {/*            <span className="info-value">{userData.email}</span>*/}
                {/*        </div>*/}
                {/*        <div className="info-item">*/}
                {/*            <span className="info-label">转专 爪专驻转</span>*/}
                {/*            <span className="info-value">{userData.joinDate}</span>*/}
                {/*        </div>*/}
                {/*    </div>*/}
                {/*</div>*/}

                <div className="bet-history">
                    <h3 className="section-title">住专转 专</h3>
                    <table className="history-table">
                        <thead>
                        <tr>
                            <th>转专</th>
                            <th>砖拽</th>
                            <th>专</th>
                            <th>转爪</th>
                            <th>拽转</th>
                        </tr>
                        </thead>
                        <tbody>
                        {betHistory && betHistory.map((bet, index) => (
                            <tr key={index}>
                                <td>{bet.date}</td>
                                <td>{bet.game}</td>
                                <td>{bet.bet}</td>
                                <td className={bet.result === '' ? 'result-win' : 'result-loss'}>
                                    {bet.result}
                                </td>
                                <td className={bet.points.startsWith('+') ? 'points-positive' : 'points-negative'}>
                                    {bet.points}
                                </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                </div>
            </div>
            <BottomMenu/>
        </div>
    );
};

export default UserProfile;