import React, { useState } from 'react';
import './rules.css';
import BottomMenu from "./BottomMenu";

const RulesComponent = () => {
    const [expandedSection, setExpandedSection] = useState(null);

    const rulesData = [
        {
            id: 1,
            title: "拽 ",
            icon: "",
            rules: [
                "拽  1",
                "拽  2",
                "拽  3",
            ]
        },
        {
            id: 2,
            title: "注专转 拽",
            icon: "",
            rules: [
                "拽 拽 1",
                "拽 拽 2",
                "拽 拽 3",
            ]
        },
        {
            id: 3,
            title: "砖 转",
            icon: "锔",
            rules: [
                " 1",
                " 2",
                " 3",
            ]
        },
        {
            id: 4,
            title: "驻专住 注砖",
            icon: "",
            rules: [
                "拽 驻专住 1",
                "拽 驻专住 2",
                "拽 驻专住 3",
            ]
        }
    ];

    const toggleSection = (id) => {
        setExpandedSection(expandedSection === id ? null : id);
    };

    return (
        <div className="rules-container">
            {/* Header */}
            <div className="rules-header">
                <h1>拽 砖拽</h1>
                <p>  砖爪专 注转  转 砖拽</p>
            </div>

            {/* Rules Content */}
            <div className="rules-content">
                {rulesData.map((section) => (
                    <div key={section.id} className="rules-section">
                        <button
                            className="section-button"
                            onClick={() => toggleSection(section.id)}
                        >
                            <div className="section-header">
                                <span className="section-icon">{section.icon}</span>
                                <h2 className="section-title">{section.title}</h2>
                            </div>
                            <span className={`arrow-icon ${expandedSection === section.id ? 'expanded' : ''}`}>
                
              </span>
                        </button>

                        <div className={`section-content ${expandedSection === section.id ? 'expanded' : ''}`}>
                            <div className="section-content-inner">
                                <ul className="rules-list">
                                    {section.rules.map((rule, index) => (
                                        <li key={index}>{rule}</li>
                                    ))}
                                </ul>
                            </div>
                        </div>
                    </div>
                ))}
            </div>

            {/* Quick Navigation */}
            <div className="quick-nav">
                <h3> 专</h3>
                <div className="quick-nav-buttons">
                    {rulesData.map((section) => (
                        <button
                            key={section.id}
                            onClick={() => toggleSection(section.id)}
                            className={`quick-nav-button ${
                                expandedSection === section.id ? 'active' : ''
                            }`}
                        >
                            {section.icon} {section.title}
                        </button>
                    ))}
                </div>
            </div>
            <BottomMenu/>
        </div>
    );
};

export default RulesComponent;